.skills-effect:hover{
    scale: 1.2;
}

@media (width <= 660px){
    .skills-container{
        font-size: 70%;
    }
    .skills-container__grid{
        grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
        grid-auto-rows: 8rem;

    }
    .skills-container__title{
        max-width: 100%;
        min-width: fit-content;
        width: 100%;
        text-align: center;
        margin: 0;
        padding: 0;
        border-top-right-radius: 0;
    }
}
